/** HAMBURGER MENU **/
rect {
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}
.side-panel.show rect:nth-child(1) {
  -webkit-transform: rotate(45deg) scaleX(0.5);
      -ms-transform: rotate(45deg) scaleX(0.5);
          transform: rotate(45deg) scaleX(0.5);
  -webkit-transform-origin: 29px 35px;
      -ms-transform-origin: 29px 35px;
          transform-origin: 29px 35px;
}
.side-panel.show rect:nth-child(3) {
  -webkit-transform: rotate(-45deg) scaleX(0.5);
      -ms-transform: rotate(-45deg) scaleX(0.5);
          transform: rotate(-45deg) scaleX(0.5);
  -webkit-transform-origin: 30px 24px;
      -ms-transform-origin: 30px 24px;
          transform-origin: 30px 24px;
}



